.App {
  font-family: Avenir;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  width: 150px;
  height: 150px; 
  padding: 20px; 
  float: left; 
}

.App-header {
  padding: 20px;
  background-color: #800000;
  height: 200px;
  color: white; 
  text-align: center;
}

.App-header .Header-desc{
  text-align: center; 
  padding-left: 20px; 
}

.App-intro {
  color: black; 
  text-align: center;
  font-size: large;
}

